
import ImageService from '../image-service';
import { Options, Vue } from 'vue-class-component';
import { Swiper, SwiperSlide } from 'swiper/vue';

@Options({
   components: { Swiper, SwiperSlide },
})
export default class HelloWorld extends Vue {
   service = new ImageService();
   list = this.service.getModels(); //.slice(0, 2);
   step = 0;
   score: number[] = [];
   ratio = false;
   logic = false;
   sens = false;
   good = false;
   swiperRef: any = {};
   inChange = false;
   inAnimation = false;
   progress = 0;
   isLoaded = false;

   created() {
      this.reset();
      setInterval(() => this.sliderMove(), 10);
      window.addEventListener('keyleft', () => this.slide(0), true);
      window.addEventListener('keyright', () => this.slide(2), true);
      window.addEventListener(
         'keyup',
         (event) => {
            if (event.key == 'ArrowLeft') this.slide(2);
            if (event.key == 'ArrowRight') this.slide(0);
         },
         true
      );
   }

   reset() {
      this.step = 0;
      this.progress = 0;
      for (let i = 0; i < 8; i++) this.score[i] = 0;
   }

   setSwiperRef(swiper: any) {
      this.isLoaded = true;
      this.swiperRef = swiper;
      this.swiperRef.activeIndex = 1;
      console.log('Service has started');
   }

   slide(index: number) {
      /*const animationStep = 10;
      const animationTime = 500;
      const direction = index - 1;
      let tempProgress = 0;
      const update = setInterval(() => {
         tempProgress += animationStep * 2;
         this.progress = (direction * tempProgress) / animationTime;
         if (tempProgress >= animationTime) {
            window.clearInterval(update);
            this.onSlideChange();
         }
      }, animationStep);*/
      if (this.step >= this.list.length || this.inAnimation) return;
      this.swiperRef.slideTo(index, 500, false);
      setTimeout(() => {
         this.onSlideChange();
      }, 200);
   }

   onSlideChange() {
      if (this.inChange || this.step >= this.list.length) return;
      if (this.progress === -1) {
         this.score[0] += this.list[this.step].ratio;
         this.score[1] += this.list[this.step].irratio;
         this.score[2] += this.list[this.step].logic;
         this.score[3] += this.list[this.step].etic;
         this.score[4] += this.list[this.step].sensor;
         this.score[5] += this.list[this.step].intuit;
         this.score[6] += this.list[this.step].good;
         this.score[7] += this.list[this.step].bad;
      }

      this.inChange = true;
      this.step++;
      this.swiperRef.slideTo(1, 0, false);
      this.progress = 0;
      this.inChange = false;
      this.inAnimation = false;
   }

   sliderMove() {
      this.progress = this.swiperRef.progress * 2 - 1 ?? 0;
   }

   transitionStart() {
      this.inAnimation = true;
   }

   transitionEnd() {
      this.inAnimation = false;
   }

   degree() {
      return this.progress * 20;
   }

   abs(): number {
      return Math.abs(this.progress);
   }

   percent(): number {
      return this.abs() > 1 ? this.abs() : 30 - this.abs() * 30;
   }

   slideResetTransitionStart() {
      this.progress = 0;
   }

   result(): string {
      this.ratio = this.score[0] > this.score[1];
      this.logic = this.score[2] > this.score[3];
      this.sens = this.score[4] > this.score[5];
      this.good = this.score[6] > this.score[7];

      if (this.ratio) {
         if (this.good) {
            if (this.logic) {
               if (this.sens) return 'Штир';
               else return 'Роб';
            } else {
               if (this.sens) return 'Гюго';
               else return 'Дост';
            }
         } else {
            if (this.logic) {
               if (this.sens) return 'Макс';
               else return 'Джек';
            } else {
               if (this.sens) return 'Драй';
               else return 'Гамло';
            }
         }
      } else {
         if (this.good) {
            if (this.logic) {
               if (this.sens) return 'Габен';
               else return 'Дон';
            } else {
               if (this.sens) return 'Дюма';
               else return 'Гексли';
            }
         } else {
            if (this.logic) {
               if (this.sens) return 'Жук';
               else return 'Баль';
            } else {
               if (this.sens) return 'Нап';
               else return 'Ёся';
            }
         }
      }
   }
}
